@import "../../variables.scss";

.DarkModeSwitch {
    position: relative;
    display: inline-block;
    top: 1em;
    right: 3.5em;
    cursor: pointer;
    opacity: 0.7;
    z-index: 10003;
    transition: all 0.3s ease;
    & input {
        opacity: 0;
        width: 0;
        height: 0;
    }
    &:hover {
        opacity: 1;
    }
}

.MenuBtn {
    position: fixed;
    display: inline-block;
    top: -2000px;
    left: 20px;
    cursor: pointer;
    filter: drop-shadow(-2px 2px 3px rgb(0 0 0 / 0.4));
    z-index: 10003;
    & input {
        opacity: 0;
        width: 0;
        height: 0;
    }
    &:hover {
        animation: rainbowShadow .7s infinite;
    }
}

.fadeOut {
    animation: fadeOut 0.7s ease-in-out forwards;
    transform-origin: center;
}

.fadeIn {
    animation: fadeIn 0.7s ease-in-out forwards;
    transform-origin: center;
}

@keyframes fadeOut {
    0% {
        opacity: 1;
        transform: translateY(0px);
    }

    35% {
        opacity: 0;
    }

    100% {
        opacity: 0;
        transform: translateY(-100px);
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        transform: translateY(-100px);
    }

    75% {
        opacity: 0;
    }
    100% {
        opacity: 1;
        transform: translateY(0px);
    }
}

@keyframes rainbowShadow {
    0% {
        filter: drop-shadow(-2px 2px 3px #000000);
    }
    10% {
        filter: drop-shadow(-2px 0px 3px #ff0000);
    }
    20% {
        filter: drop-shadow(0px 0px 3px #ff7f00);
    }
    30% {
        filter: drop-shadow(0px -2px 3px #ffff00);
    }
    40% {
        filter: drop-shadow(2px -2px 3px #00ff00);
    }
    50% {
        filter: drop-shadow(2px 0px 3px #0000ff);
    }
    60% {
        filter: drop-shadow(2px 2px 3px #4b0082);
    }
    70% {
        filter: drop-shadow(0px 2px 3px #9400d3);
    }
    80% {
        filter: drop-shadow(-2px 2px 3px #ee82ee);
    }
    90% {
        filter: drop-shadow(-2px 2px 3px #ff1493);
    }
    100% {
        filter: drop-shadow(-2px 2px 3px #000000);
    }
}

@media (max-width: 1150px) {
    .MenuBtn {
        position: fixed;
        top: 20px;
    }
    .DarkModeSwitch {
        position: fixed;
        top: -1990px;
    }
}