@import "variables";

html, body {
  margin: 0;
  padding: 0;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  color: $text-color;
  background-color: $background-color;
}

.Main {
  position: relative;
  display: grid;
  grid-template-columns: 3fr 2fr 2fr;
  grid-template-rows: 1fr 3fr 2fr 1fr;
  grid-template-areas:
    "header header header"
    "about projects projects"
    "about certificates blog"
    "footer footer footer";
  gap: 10px;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  z-index: 10000;
}

.Header {
  grid-area: header;
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  width: 100%;
  margin-bottom: 0px;
  z-index: 10003;
  .menuBg {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 3em;
    top: -2000px;
    left: 1vw;
    width: 95vw;
    height: 95vh;
    opacity: 0.9;
    border-radius: 5%;
    background-color: $bento-color;
    z-index: 10000;
    .menuOptions {
      text-decoration: none;
      color: $background-color;
      font-family: "Montserrat", sans-serif;
      font-size: 3rem;
      font-weight: 700;
    }
  }
}

.About {
  grid-area: about;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
  min-height: 40em;
  min-width: 21em;
  // max-width: 55ch;
  margin: 0.5em 0em 5em 5em;
  border-radius: 2% 2% 15% 2%;
  padding: 1em;
  z-index: 10002;
  box-shadow: inset 0 0 20px $bento-color;
  .logo {
    position: relative;
    display: inline-block;
    margin-right: -119px;
    margin-left: 2em;
    top: 0;
    cursor: pointer;
    filter: drop-shadow(0 0 10px $background-color);
    z-index: 10000;
    transition: all 0.3s ease;
    &:hover {
      transform: scale(1.1);
    }
  }
  .photo {
    position: relative;
    top: 3px;
    margin-left: 0;
    filter: drop-shadow(0 0 10px $background-color);
    z-index: 9999;
  }
  &Article {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    max-width: 47ch;
    margin-left: 1em;
    margin-right: 5em;
    z-index: 10002;
  }
  &Title {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 0;
  }
  &Text {
    font-size: 1.2rem;
    font-weight: 400;
    margin-bottom: 0;
  }
}

.Projects {
  grid-area: projects;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow: auto;
  scrollbar-width: none;
  ::-webkit-scrollbar {
    display: none;
  }
  min-width: 21em;
  margin-top: 0.5em;
  margin-right: 2em;
  border-radius: 2% 2% 15% 2%;
  padding: 20px;
  z-index: 10002;
  box-shadow: inset 0 0 20px $bento-color;
  &Title {
    cursor: pointer;
    &:hover {
      transform: scale(1.1);
    }
  }
}

.Project {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  &box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 9em;
    width: 18em;
    margin: 1em;
    box-shadow: inset 0 0 10px $bento-color;
    border-radius: 10%;
    cursor: pointer;
    z-index: 11000;
    transition: all 0.3s ease;
    &:hover {
      transform: scale(1.1);
    }
  }
  &Imgs {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 0.5em;
    gap: 0.2em;
    img {
      max-height: 3em;
      border-radius: 10%;
    }
  }
}

.Certificates {
  grid-area: certificates;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow: auto;
  scrollbar-width: none;
  ::-webkit-scrollbar {
    display: none;
  }
  min-height: 11em;
  border-radius: 2% 2% 15% 2%;
  margin-bottom: 5em;
  padding: 20px;
  z-index: 10002;
  box-shadow: inset 0 0 20px $bento-color;
  &Title {
    cursor: pointer;
    &:hover {
      transform: scale(1.1);
    }
  }
  &Content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
  }
  &Box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    img {
      max-height: 5em;
      border-radius: 10%;
    }
  }
  &Text {
    font-size: 0.9rem;
    font-weight: 400;
    margin-bottom: 0;
  }
}

.Blog {
  grid-area: blog;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow: auto;
  scrollbar-width: none;
  ::-webkit-scrollbar {
    display: none;
  }
  min-height: 11em;
  margin-right: 2em;
  margin-bottom: 5em;
  border-radius: 2% 2% 15% 2%;
  padding: 20px;
  z-index: 10002;
  box-shadow: inset 0 0 20px $bento-color;
  &Title {
    cursor: pointer;
    &:hover {
      transform: scale(1.1);
    }
  }
  &Content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1em;
    &Box {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      box-shadow: inset 0 0 10px $bento-color;
      border-radius: 10%;
      max-width: 85%;
      img {
        max-width: 10em;
        border-radius: 10%;
      }
    }
    &Title {
      font-size: 1.2rem;
      font-weight: 700;
      margin-bottom: 0;
    }
    &Text {
      max-width: 45ch;
      font-size: 0.9rem;
      font-weight: 400;
      margin-left: 1em;
      margin-right: 1em;
      margin-bottom: 1em;
    }
  }
}

.Footer {
  grid-area: footer;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end;
  right: 3.5em;
  bottom: 2em;
  z-index: 10004;
  &Icon {
    margin: 0 0.5em;
    color: $bento-color;
    transition: all 0.3s ease;
    &:hover {
      transform: scale(1.1);
    }
  }
}

.menuOpen {
  animation: menuOpen 1s forwards;
}

.menuClose {
  animation: menuClose 1s forwards;
}

.lightBg {
  animation: lightBg 1s forwards;
}

.darkBg {
  animation: darkBg 1s forwards;
}

.scaleIn {
  animation: scaleIn 1s forwards;
}

.scaleOut {
  animation: scaleOut 1s forwards;
}

.ProjectsMax {
  animation: ProjectsMax 1s forwards;
}

.CertificatesMax {
  animation: CertificatesMax 1s forwards;
}

.BlogMax {
  animation: BlogMax 1s forwards;
}

@keyframes menuOpen {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(2010px);
  }
}

@keyframes menuClose {
  0% {
    transform: translateY(2010px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes darkBg {
  0% {
    background-color: $background-color;
    color: $text-color;
  }
  100% {
    background-color: $dark-background-color;
    color: $background-color;
  }
}

@keyframes lightBg {
  0% {
    background-color: $dark-background-color;
    color: $background-color;
  }
  100% {
    background-color: $background-color;
    color: $text-color;
  }
}

@keyframes scaleIn {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes scaleOut {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}

@keyframes ProjectsMax {
  0% {
    transform: scale(1) translate(0, 0);
  }
  100% {
    transform: scale(1.5) translate(-15em, 5em);

  }
}

@keyframes CertificatesMax {
  0% {
    transform: scale(1) translate(0, 0);
  }
  100% {
    transform: scale(3) translate(-2em, -5em);
  }
}

@keyframes BlogMax {
  0% {
    transform: scale(1) translate(0, 0);
  }
  100% {
    transform: scale(3) translate(-13em, -5em);
  }
}

@media (max-width: 1150px) {
  .App {
    display: flex;
    flex-direction: column;
  }

  .About {
    justify-content: center;
    align-items: center;
    min-height: 40em;
    margin: 5em;
  }

  .Projects {
    min-height: 40em;
    margin: 5em;
    &Title {
      margin-top: 1em;
      cursor: auto;
      &:hover {
        transform: scale(1);
      }
    }
  }

  .Project {
    grid-template-columns: repeat(1, 1fr);
    &box {
      height: 10em;
      width: 20em;
    }
  }

  .Certificates {
    min-height: 40em;
    margin: 5em;
    &Title {
      cursor:auto;
      &:hover {
        transform: scale(1);
      }
    }
  }

  .Blog {
    min-height: 40em;
    margin: 5em;
    &Title {
      cursor: auto;
      &:hover {
        transform: scale(1);
      }
    }
  }

  .Footer {
    position: fixed;
    bottom: 2070px;
    &Icon {
      color: $background-color;
      z-index: 10005;
    }
    }

    @keyframes scaleIn {
      0% {
        transform: scale(1);
      }
      100% {
        transform: scale(1);
      }
    }

    @keyframes scaleOut {
      0% {
        transform: scale(1);
      }
      100% {
        transform: scale(1);
      }
    }

    @keyframes ProjectsMax {
      0% {
        transform: scale(1) translate(0, 0);
      }
      100% {
        transform: scale(1) translate(0em, 0em);
      }
    }

    @keyframes CertificatesMax {
      0% {
        transform: scale(1) translate(0, 0);
      }
      100% {
        transform: scale(1) translate(0em, 0em);
      }
    }

    @keyframes BlogMax {
      0% {
        transform: scale(1) translate(0, 0);
      }
      100% {
        transform: scale(1) translate(0em, 0em);
      }
    }
}